import { storage } from '@/utils/storage'

const state = {
  user: {
    username: null,
    socketId: null
  }
}

const mutations = {
  setUser (state, user) {
    state.user = user
  }
}

const getters = {
  user (state) {
    return state.user
  }
}

const actions = {
  setUser (context, data) {
    console.log(data)
    context.commit('setUser', data)
    storage.storeValue('user', data)
  },
  killUser (context) {
    storage.clearValue('user')
  }
}

export default {
  state,
  actions,
  mutations,
  getters
}



